* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.heading {
  padding: 2rem 3rem;
}

.heading > h1 {
  text-transform: capitalize;
  font-size: 2rem;
}

.heading > span {
  display: block;
  font-size: 1.2rem;
  margin-top: 2rem;
}

.display {
  width: 95vw;
  padding: 1rem 0;
  display: flex;
  text-align: center;
  justify-content: center;
  font-size: 4rem;
  font-weight: bold;
  color: #27ae60;
  background-color: #f0f8ff;
  border-radius: 1rem;
}

.input-form {
  display: grid;
  grid-template-columns: 4fr 1.4fr;
  width: 100vw;
  /* max-width: 500px; */
  max-width: 100vw;
}

.input-field {
  background-color: rgb(245, 226, 197);
  outline: none;
  border: none;
  font-size: 1.3rem;
  font-weight: bold;
  padding: 1rem 0;
  text-transform: uppercase;
  text-align: center;
}

.input-field::-webkit-input-placeholder {
  text-transform: capitalize;
}

.input-field::placeholder {
  text-transform: capitalize;
}

.reset {
  outline: none;
  border: none;
  background-color: #ff0000;
  color: #fff;
  font-size: 1.3rem;
}

.App {
  min-height: 100vh;
  width: 100vw;
  background-color: #033e9c;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  grid-gap: 2rem;
  gap: 2rem;
}

.timer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.curr-time > span:nth-of-type(1),
.best-score > span:nth-of-type(1) {
  font-weight: bold;
  margin-right: 0.5rem;
}

.credit {
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.9);
  color: black;
  position: fixed;
  height: 100%;
  width: 100%;
  z-index: 100;
  transition: all 1s ease;
}

.credit > input {
  display: none;
}

.credit.closed {
  -webkit-animation-name: dismiss, hide;
          animation-name: dismiss, hide;
  -webkit-animation-duration: 300ms;
          animation-duration: 300ms;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-delay: 0s, 100ms;
          animation-delay: 0s, 100ms;
}

.close-credit {
  position: absolute;
  top: 7rem;
  right: 1rem;
  font-size: 3rem;
  color: #000;
  cursor: pointer;
}

.credit-detail {
  background: #fff;
  border: 0.1rem solid rgba(0, 0, 0, 0.1);
  width: 100vw;
  padding: 3rem;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
  border-radius: 0.5rem;
}

.credit-detail h1:nth-child(2) {
  color: #033e9c;
}

.credit-detail > span {
  display: block;
  margin: 0.5rem 0;
}

.credit-detail h2:nth-child(4) {
  color: #27ae60;
}

@-webkit-keyframes dismiss {
  0% {
    opacity: 1;
  }
  90%,
  100% {
    opacity: 0;
    font-size: 0.1px;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}

@keyframes dismiss {
  0% {
    opacity: 1;
  }
  90%,
  100% {
    opacity: 0;
    font-size: 0.1px;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}

@-webkit-keyframes hide {
  100% {
    height: 0px;
    width: 0px;
    overflow: hidden;
    margin: 0px;
    padding: 0px;
    border: 0px;
  }
}

@keyframes hide {
  100% {
    height: 0px;
    width: 0px;
    overflow: hidden;
    margin: 0px;
    padding: 0px;
    border: 0px;
  }
}

